export default {
  name: 'index',
  components: {
  },

  data() {
    return {
      five_number:'0-0-0-0-0',
      two_number:'0-0'
    }
  },

  mounted() {


  },
  created() {
    // this.getStatis()
    // this.getHealth()
  },
  methods: {
    produce(idx){
      console.log(idx);
      var arr = [];
      var total_nums = 0;//数字最大直
      if(idx==0){
        for(var i=0;i<5;i++){
          total_nums = 35;
          this.getx(i,arr,total_nums,idx);
        }
      }else if(idx==1){
        for(var i=0;i<2;i++){
          total_nums = 12;
          this.getx(i,arr,total_nums,idx);
        }
      }
    },
    getx(ii,arr,total_nums,idx){
      for(var i=0;i>-1;i++){
             var flag = true;
             var num = Math.floor(Math.random()*total_nums)+1;
             for(var i in arr){
                 if(arr[i] == num){
                     flag= false;
                     break;
                 }
             }
             if(flag == true){
                 arr.push(num);
                 if(idx==0){
                   console.log(arr);
                   this.five_number = arr.join('-');
                 }else if(idx==1){
                   this.two_number = arr.join('-');
                 }
                 return;
             }
         }
    }
  }
}



